.programs {
  width: 100%;
  min-width: 1024px;
  max-width: 1366px;
  margin: 0 auto;
  padding-top: 107px;
  padding-bottom: 10px;


  background-image: url("../img/sketch-bg-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: right;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/sketch-bg-desktop@2x.jpg");
    background-size: contain;
  }

  @media (max-width: $tablet-width) {
    min-width: 768px;
    max-width: 1023px;
  }

  @media (max-width: $mobile-width) {
    min-width: 320px;
    max-width: 767px;
    padding-top: 0;

    background-image: url("../img/sketch-bg-mobile@1x.jpg");
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/sketch-bg-mobile@2x.jpg");
      background-size: contain;
    }
  }
}

.programs__wrap {
  width: 945px;
  margin: 0 auto;

  @media (max-width: $tablet-width) {
    width: 675px;
    margin: 0 auto;
  }

  @media (max-width: $mobile-width) {
    width: 320px;
    flex-direction: column;
    padding-left: 0;
    padding-right: 0;
  }
}

.programs h2 {
  @include title;
  font-size: 40px;
  line-height: calc(49 / 40);
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 65px;

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    line-height: 1.22;
  }

  @media (max-width: $mobile-width) {
    font-size: 32px;
    line-height: calc(39 / 32);
    text-align: center;
    margin-bottom: 16px;
  }
}

.programs__inner {
  display: flex;

  @media (max-width: $mobile-width) {
    flex-direction: column;
  }
}

.programs__tab {
  width: 230px;
  margin-right: 30px;

  @media (max-width: $mobile-width) {
    width: 362px;
    margin-right: 0;
    margin-left: -42px;
    display: flex;
    margin-bottom: 28px;
    overflow: hidden;
    position: relative;
  }

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    width: auto;
  }
}

.programs__button {
  line-height: calc(18 / 15);
  border: none;
  background-color: transparent;
  opacity: 0.87;
  letter-spacing: 0.8px;

  text-align: left;

  width: 228px;
  height: 68px;
  margin-bottom: 4px;
  padding-left: 72px;

  position: relative;
  cursor: pointer;

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    line-height: 1.2;
  }

  @media (max-width: $mobile-width) {
    color: rgba($text, 0.4);
    letter-spacing: 1px;

    width: 187px;
    height: 48px;

    padding-left: 52px;
    margin-right: 4px;
  }
}

.programs__button:hover {
  background-image: $gradient;
  background-size: 100%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    background-image: none;
    color: $accent-decor-1;
  }
}

.programs__button--general::before {
  content: "";
  position: absolute;
  left: 20px;
  top: 18px;
  width: 36px;
  height: 36px;
  background-image: url("../img/black-general.svg");
  background-repeat: no-repeat;

  @media (max-width: $mobile-width) {
    left: 10px;
    top: 7px;
  }
}

.programs__button--general:hover::before {
  left: 24px;
  top: 20px;
  background-image: url("../img/yellow-general.svg");

