.programs__box {
  width: 100vw;
  padding-top: 18px;


  @media (max-width: $mobile-width) {
    width: inherit;
    padding-top: 0;
  }
}

.programs__item {
  padding-left: 4.6%;
  padding-right: 4.6%;
}

.programs__item.active {
  display: block;
}

.programs__item h3 {
  @include title;
  font-size: 28px;
  line-height: calc(34 / 28);
  margin: 0;
  margin-bottom: 24px;

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    line-height: 1.21;
  }

  @media (max-width: $mobile-width) {
    font-size: 24px;
    line-height: calc(29 / 24);
    margin-bottom: 15px;
  }
}

.programs__item p {
  width: 550px;

  @media (max-width: $tablet-width) {
    width: 400px;
  }

  @media (max-width: $mobile-width) {
    font-size: 14px;
    line-height: calc(22 / 14);
    width: auto;
    display: block;
    margin: 0 auto;
    margin-bottom: 15px;
  }
}

.programs__button--select-general,
.programs__button--select-academic,
.programs__button--select-internships,
.programs__button--select-volunteerism,
.programs__button--select-religious {
  font-weight: 600;
  font-size: 13px;
  line-height: calc(19 / 16);
  text-transform: uppercase;
  width: 228px;
  height: 68px;
  position: relative;
  color: $white;

  background-image: url("../img/rectangle.png");
  background-repeat: no-repeat;
  background-size: 228px auto;

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    line-height: 1.19;
  }

  @media (max-width: $mobile-width) {
    font-weight: 500;
    width: auto;
    height: 48px;
    border-radius: 40px;
    border: none;
  }
}

