.programs__control {
  display: none;

  @media (max-width: $mobile-width) {
    display: flex;
    width: 320px;
    height: 20px;
  }
}

.programs__control-prev {
  cursor: pointer;
  position: absolute;
  top: 20px;
  left: 40px;
  width: 60px;
  height: 50px;
  margin-top: -22px;
  padding: 16px;
  transition: 0.6s ease;
  z-index: 10;
}

.programs__control-next {
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 0;
  width: 60px;
  height: 50px;
  margin-top: -22px;
  padding: 16px;
  transition: 0.6s ease;
  z-index: 10;
}
